<template>
  <div class="cropper-content">
    <div class="cropper-box">
      <div class="cropper">
        <vue-cropper
          ref="cropper"
          :img="option.img"
          :output-size="option.outputSize"
          :output-type="option.outputType"
          :info="option.info"
          :can-scale="option.canScale"
          :auto-crop="option.autoCrop"
          :auto-crop-width="option.autoCropWidth"
          :auto-crop-height="option.autoCropHeight"
          :fixed="option.fixed"
          :fixed-number="option.fixedNumber"
          :full="option.full"
          :fixed-box="option.fixedBox"
          :can-move="option.canMove"
          :can-move-box="option.canMoveBox"
          :original="option.original"
          :center-box="option.centerBox"
          :height="option.height"
          :info-true="option.infoTrue"
          :max-img-size="option.maxImgSize"
          :enlarge="option.enlarge"
          :mode="option.mode"
          @imgLoad="imgLoad"
        />
      </div>
      <!---->
      <div class="footer-btn">
        <van-button type="info" @click="$emit('cancel')"></van-button>
        <van-button type="primary" @click="uploadImg('blob')"></van-button>
      </div>
    </div>
  </div>
</template>

<script>
import { reqUpdateUserPhoto } from '@/api/user'
import { VueCropper } from 'vue-cropper'
export default {
  name: 'CropperImage',
  components: {
    VueCropper
  },
  data () {
    return {
      option: {
        img: '', // 
        outputSize: 1, // (0.1 - 1)
        outputType: 'jpeg', // jpeg || png || webp
        info: true, // 
        canScale: true, // 
        autoCrop: true, // 
        autoCropWidth: 150, // 
        autoCropHeight: 150, // 
        fixed: true, // 
        fixedNumber: [1, 1], // 
        full: false, // false
        fixedBox: true, // 
        canMove: true, // 
        canMoveBox: false, // 
        original: false, // 
        centerBox: true, // 
        height: true, // dpr 
        infoTrue: false, // truefalse
        maxImgSize: 3000, // 
        enlarge: 1, // 
        mode: '300px 300px' // 
      }
    }
  },
  methods: {
    // 
    imgLoad (msg) {
      console.log('=====' + msg)
    },
    // 
    selectImg (e) {
      const file = e.target.files[0]
      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        this.$message({
          message: 'jpegjpgpng',
          type: 'error'
        })
        return false
      }
      // blob
      const reader = new FileReader()
      reader.onload = (e) => {
        let data
        if (typeof e.target.result === 'object') {
          data = window.URL.createObjectURL(new Blob([e.target.result]))
        } else {
          data = e.target.result
        }
        this.option.img = data
      }
      // base64
      reader.readAsDataURL(file)
    },
    // 
    async uploadImg (type) {
      this.$toast.loading('')
      if (type === 'blob') {
        // blob
        this.$refs.cropper.getCropBlob(async (data) => {
          const formData = new FormData()
          const apiKey = 'photo' // 
          formData.append(apiKey, data)

          try {
            //    ------------------
            const { data: { photo } } = await reqUpdateUserPhoto(formData)
            this.$emit('passPhotoUrl', photo)
            this.$toast.success('')
          } catch (e) {
            this.$toast.fail('')
          }
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.cropper-content{
  display: flex;
  display: -webkit-flex;
  justify-content: flex-end;
  height: 100%;

  .cropper-drag-box {
    width: 100%;
    height: 100%;
  }

  .cropper-box{
    flex: 1;
    width: 100%;
    height: 100%;
    .cropper{
      width: 100%;
      height: 100%;

    }
  }

  .show-preview{
    flex: 1;
    -webkit-flex: 1;
    display: flex;
    display: -webkit-flex;
    justify-content: center;
    .preview{
      overflow: hidden;
      border:1px solid #67c23a;
      background: #cccccc;
    }
  }
}
.footer-btn{
  z-index: 11;
  width: 100%;
  position: absolute;
  bottom: 0;
  margin-top: 30px;
  display: flex;
  display: -webkit-flex;
  justify-content: space-between;
}
</style>
