<template>
  <div class="profile">
    <!--  -->
    <van-nav-bar left-arrow @click-left="$router.back()" title=""></van-nav-bar>

    <!--  -->
    <van-cell-group>
      <van-cell is-link title="" center @click="fileSelect">
        <van-image
          slot="default"
          width="1.5rem"
          height="1.5rem"
          fit="cover"
          round
          :src="user.photo"
        />
      </van-cell>

      <van-cell is-link title="" :value="user.name" @click="showName=true"/>
      <van-cell is-link title="" :value="!user.gender? '': ''" @click="showGender=true"/>
      <van-cell is-link title="" :value="user.birthday" @click="showBirthday=true"/>
    </van-cell-group>

    <!--  -->
    <van-dialog v-model="showName" title="" show-cancel-button @confirm="updateName" @cancel="newName = user.name">
      <van-form>
        <van-field
          v-model.trim="newName"
          placeholder=""
          :rules="[{required: true, message: '', trigger:'onChange' }, {pattern: /^.{1,7}$/, message: '1-7', trigger:'onChange'}]"
        ></van-field>
      </van-form>
    </van-dialog>

    <!--  -->
    <van-popup v-model="showGender" position="bottom">
      <van-nav-bar title="" left-text="" @click-left="showGender=false">
      </van-nav-bar>
      <van-cell title="" @click="saveGender(0)" is-link></van-cell>
      <van-cell title="" @click="saveGender(1)" is-link></van-cell>
    </van-popup>

    <!--  -->
    <van-popup v-model="showBirthday" position="bottom">
      <van-datetime-picker
        @confirm="updateBirthday"
        v-model="nowDate"
        type="date"
        title=""
        :min-date="minDate"
        :max-date="maxDate"
      />
    </van-popup>

    <van-uploader ref="vanUploader" :after-read="afterRead"/>
    <div class="cropper" v-if="showCropper">
      <cropper-image
        ref="cropper"
        @passPhotoUrl="passPhotoUrl"
        @cancel="showCropper = false"
      ></cropper-image>
    </div>
  </div>
</template>

<script>
import { reqGetUserInfo, reqUpdateUserProfile } from '@/api/user'
import dayjs from 'dayjs'
import CropperImage from '@/components/CropperImage'

export default {
  name: 'userProfile',
  components: { CropperImage },
  data () {
    return {
      // 
      showName: false,
      showGender: false,
      showBirthday: false,
      // 
      user: {},
      // birthday: ""
      // gender: 0
      // id: BigNumber {s: 1, e: 18, c: Array(2)}
      // mobile: "14769979101"
      // name: "14769979101"
      // photo: "http://toutiao-img.itheima.net/Fkj6tQi3xJwVXi1u2swCElotfdCi"
      newName: '',
      minDate: new Date(1970, 0, 1), // 
      maxDate: new Date(), // 
      nowDate: new Date(), // 
      showCropper: false
    }
  },
  created () {
    this.getUserProfileData()
  },
  methods: {
    passPhotoUrl (url) {
      this.user.photo = url
      this.showCropper = false
    },
    fileSelect () {
      this.$refs.vanUploader.chooseFile()
    },
    afterRead (file) {
      this.showCropper = true
      this.$nextTick(() => {
        this.$refs.cropper.option.img = file.content
      })
    },
    async updateBirthday () {
      this.showBirthday = false
      this.$toast.loading('')
      const birthday = dayjs(this.nowDate).format('YYYY-MM-DD')
      await reqUpdateUserProfile({
        birthday
      })
      this.user.birthday = birthday
      this.$toast.success('')
    },
    async saveGender (val) {
      await reqUpdateUserProfile({
        gender: val
      })
    },
    async updateName () {
      if (this.newName === this.username) {
        this.$toast('')
        return
      }
      if (this.newName.trim().length === 0) {
        this.$toast('')
        this.newName = ''
        return
      }
      if (!/^.{1,7}$/.test(this.newName)) {
        this.$toast('1-7')
        return
      }

      await reqUpdateUserProfile({
        name: this.newName
      })
      this.user.name = this.newName
    },
    async getUserProfileData () {
      const { data } = await reqGetUserInfo()
      this.user = data
      this.newName = data.name
      this.nowDate = new Date(this.user.birthday)
    }
  }
}
</script>

<style lang="scss">
.profile {
  .van-uploader {
    opacity: 0;
  }

  .cropper {
    z-index: 10;
    /*  */
    position: absolute;
    top: 0;
    left: 0;
    /*  */
    width: 100%;
    height: 100%;
  }
}
</style>
